
.active {
    font-size: 18px!important;
    transition: .3s;
}
.nav-wrapper {
    color: white;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}
.triangles-above {
    background-image: url('../../static/images/triangle-solid.svg');
    height: 18px;
    background-repeat-y: no-repeat;
    margin: 0px;
    outline: none;
    margin-bottom: -1px;
}
nav.bottomNav {
background-color: #000;
}
nav.bottomNav>ul {
    padding: 20px 0px;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}
nav.bottomNav>ul>a {
    display: inline;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 300;
    font-size: 12px;
    font-style: italic;
    margin-left: 20px;
    -ms-flex: 1;
        flex: 1;
    color: #fff;
    text-transform: uppercase;
    text-decoration: underline;
}
nav.bottomNav>ul>a>li:last-of-type {

    margin-right: 20px;
}




